оступності обладнана конкретна будівля. За допомогою об’єднаних даних щодо дозволів, тендерів та позатендерних закупівель, які можна знайти на сторінці власника кожного об’єкта, громадськість матиме додатковий інструмент контролю за тим, наскільки ефективно витрачаються кошти щодо облаштування засобів доступності в місті. Щодо об’єктів міської інфраструктури сервіс надасть можливість ознайомитись з інформацією щодо стану засобів доступності, що була встановлена в ході перевірок комунальних об’єктів. Це дозволить жителям міста оцінити відповідність заявленого стану будівель реальній ситуації в місті та краще комунікувати із місцевою владою щодо необхідності проведення додаткових перевірок та нових ремонтних робіт на об’єктах. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"section-block"},[_c('p',{staticClass:"block-header"},[_vm._v("Доступна навігація")]),_c('p',{staticClass:"block-text"},[_vm._v(" На мапі нижче відображені комунальні об’єкти міста, що містяться в наборі даних з доступності Запорізької міської ради. Різними кольорами відмічені типи організацій, що знаходяться на об’єкті. За допомогою галочки ви можете фільтрувати об’єкти на мапі, залишивши галочку лише для того засобу доступності, який хочете побачити на карті. При наведенні на маркер з’явиться спливаюче віконце із назвою об’єкту. Ознайомитись з більш детальною інформацією про об’єкт та засоби доступності на ньому можна, клікнувши на це віконце. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"how-to-border"},[_c('div',{staticClass:"how-to-bar"},[_c('div',{staticClass:"pointer-icon"},[_c('img',{attrs:{"src":require("./assets/images/pointer-icon.png")}})]),_c('p',{staticClass:"how-to-title"},[_vm._v(" Не знайшли об'єкт на карті? Додайте його власноруч! ")])]),_c('div',{staticClass:"how-to-description"},[_c('p',[_vm._v(" Сервіс також дозволяє додавати власні об’єкти на карту. Наприклад, ви хотіли б додати інформацію про магазин в місті, в якому обладнаний пандус, щоб інші жителі також могли це побачити. Для цього вам необхідно зайти на цю сторінку зі свого персонального комп'ютера або ноутбука, затиснути клавішу Ctrl (для Windows) або ⌘ (для Mac) та, не відпускаючи її, клікнути на карті, щоб створити точку. Після кліку з’явиться форма, в якій необхідно заповнити дані щодо цього об’єкту. Після цього дані відправляться на модерацію, і об’єкт буде доданий після його перевірки. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"section-block"},[_c('p',{staticClass:"block-header"},[_vm._v("Якими засобами обладнані місцеві будівлі?")]),_c('p',{staticClass:"block-text"},[_vm._v(" Нижче доступна розгорнута інформація по кожному з об’єктів міста. Виберіть тип організації та клікніть по назві об’єкту або на знак + у правій частині екрану. Після цього відкриється картка об’єкту, в якій можна побачити деталі щодо засобів доступності, які були встановлені в ході перевірки об’єкту, та обсяг фінансування, виділений на відповідні роботи. Клікніть по назві організації, яка розміщена внизу кожної картки, щоб перейти до інформації про дозволи та тендери на будівельні/ремонтні роботи. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"call-to-action-container"},[_c('div',{staticClass:"call-to-action"},[_c('div',{staticClass:"block-background"},[_c('p',[_vm._v(" Дані нашого проєкту можуть допомогти вам оцінити стан доступності конкретної будівлі. Щодо об’єктів, які відносяться до інфраструктури міста, ви можете побачити, які роботи проводились на об’єкті і коли востаннє проводилась перевірка. ")]),_c('div',{staticClass:"title-container"},[_c('div',{staticClass:"pointer-icon"},[_c('img',{attrs:{"src":require("./assets/images/pointer-icon.png")}})]),_c('p',{staticClass:"title-small"},[_vm._v(" Що робити, якщо ви помітили невідпові